@import 'global/vars.scss';
@import 'global/mixin.scss';

.widget-area {
   
    flex-basis: 310px;
    flex-shrink: 0;
    position: relative;

    @media screen and (max-width: 1023px) { 

        width: 100%;
        padding: 10px;
        box-sizing: border-box;
    }
    

    > .items { margin: 0;
        
        &.back { 

            border:30px solid #eff8fd;
            margin-bottom: 30px;

            img { width: 100%; }
        
            &:hover  a, 
            &:hover .meta-span { color: $color-green3; }
        }

        &.border {

            border-left: 1px solid #eff8fd;
            border-right: 1px solid #eff8fd;
            
            &:last-of-type { border-bottom: 1px solid #eff8fd; }

            &:hover { background-color: $color-green3;}

            &:hover  .items-title-articl, 
            &:hover .meta-span { color: $color-white; }
                
          .items-title-articl { 

                text-align: left;
                font-size: 16px;
                padding-top: 20px;   
            }

            .meta {  

                justify-content: flex-start;
                padding: 10px 10px 0;
                
                .messag { margin-left: 30px; }
            }
        }

       .meta-span { color: $color-blue3; }
    }
}

.short-navig { padding-top: 7px; }

.row-navig {

    background-color: #eef8fd;
    border-bottom: 1px solid $color-white;
    font-family: $font-sansbold;
    font-size: 18px;
    padding: 16px 20px;
    cursor: pointer;

    &.opened:before { @include pseudo(inline-block, "\f0d8");}
        
    &:before {

        @include pseudo(inline-block, "\f0d7");
        font-family: $font-awesome;
        padding-right: 20px;
    }
}

.row-navig:hover,
.row-navig.opened:hover,
.hidd:hover { 

    background-color: $color-green3;
    color:$color-white; 
}

.total-number {

    font-family: $font-sans;
    float: right;
}

.hidden-block { display: none; 

    &.opened { display: block; }

    .hidd {

       
        font-family: $font-sans;
        padding: 16px 20px 16px 47px;
        border-top: 0;
    }
}

.asadvert-aside {

    width: 100%;
    height: 446px;
    padding: 30px 0;

    img { margin: 0 auto; }
}

.title-aside {

    background-image: url(/images/background/blue_sitebar.png);
    background-repeat: repeat;
    @include font($font-sansbold, $color-white, 18px);
    padding: 16px 20px;

    &:before {

        @include pseudo(inline-block, "");
    
        width: 20px;
        border-bottom: 1px solid $color-white;
        margin-right: 20px;
        vertical-align: middle;
    } 
}
